
































import Vue from "vue";
export default Vue.extend({
	data() {
		return {
			post: {} as any,
			comments: [] as Object[],
			createContent: ""
		};
	},
	created() {
		this.postReload();
		this.commentReload();
	},
	methods: {
		enterPress(e: KeyboardEvent) {
			if (e.keyCode == 13) this.commentwrite();
		},
		commentwrite() {
			this.$store
				.dispatch("WRITE_COMMENT", {
					postPk: this.getPostPk,
					content: this.createContent
				})
				.then(data => {
					this.commentReload();
				})
				.catch(err => console.dir(err));
		},
		recommend() {
			this.$store
				.dispatch("RECOMMEND_POST", {
					groupPk: this.getGroupPk,
					postPk: this.getPostPk
				})
				.then(data => {
					this.postReload();
				})
				.catch(err => console.dir(err));
		},
		removepost() {
			this.$store
				.dispatch("REMOVE_POST", {
					groupPk: this.getGroupPk,
					postPk: this.getPostPk
				})
				.then(data => {
					this.$router.back();
				})
				.catch(err => console.dir(err));
		},
		postReload() {
			this.$store
				.dispatch("GET_POST", {
					groupPk: this.getGroupPk,
					postPk: this.getPostPk
				})
				.then(data => {
					this.post = data;
				})
				.catch(err => console.dir(err));
		},
		commentReload() {
			this.createContent = "";
			this.$store
				.dispatch("GET_POST_COMMENTS", this.getPostPk)
				.then(data => {
					this.comments = data.reverse();
				})
				.catch(err => console.dir(err));
		}
	},
	computed: {
		getGroupPk() {
			return this.$route.params.pk;
		},
		getPostPk() {
			return this.$route.params.postpk;
		},
		getDate(): string {
			if (this.post.created_at)
				return new Date(this.post.created_at).toLocaleString();
			else return "";
		},
		getUser() {
			return this.$store.state.user;
		}
	}
});
