























import Vue from "vue";
export default Vue.extend({
	data() {
		return {
			searchString: ""
		};
	},
	created() {
		this.$store.dispatch("GET_PROFILE").then(user => {
			console.log(user);
		});
	},
	methods: {
		enterPress(e: KeyboardEvent) {
			if (e.keyCode == 13) this.search();
		},
		search() {
			this.$router.push({
				name: "search",
				query: {
					search: this.searchString
				}
			});
		}
	}
});
