







import Vue from "vue";
export default Vue.extend({
	data() {
		return {
			group: {},
		};
	},
	created() {
		this.$store
			.dispatch("GET_GROUP", this.getGroupPk)
			.then(data => {
				this.group = data;
			})
            .catch(err => this.$router.back());
	},
	computed: {
		getGroupPk() {
			return this.$route.params.pk;
		}
	}
});
