



































import Vue from "vue";
import PostCardView from "@/components/PostCardView.vue";
import GroupCardView from "@/components/GroupCardView.vue";
export default Vue.extend({
	components: {
		PostCardView,
		GroupCardView
	},
	data() {
		return {
            gropus: [],
            username : "",
            password : ""
		};
	},
	created() {
		this.$store.dispatch("GET_GROUPS").then(groups => {
			this.gropus = groups;
		});
	},
	methods: {
		login() {
			this.$store
				.dispatch("LOGIN", {
					username: this.username,
					password: this.password
				})
				.then(data => {
                    alert("로그인 성공")
				})
				.catch(err => {
					console.log(err);
				});
		},
		register() {
			this.$store
				.dispatch("REGISTER", {
					username: this.username,
					password: this.password
				})
				.then(data => {
                    alert("회원가입 성공")
                    this.username = ""
                    this.password = ""
				})
				.catch(err => {
					console.log(err);
				});
		},
		logout() {
			this.$store
				.dispatch("LOGOUT")
				.then(data => {})
				.catch(err => console.log(err));
		}
	},
	computed: {
		getUser() {
			return this.$store.state.user;
		}
	}
});
