












import Vue from "vue";
export default Vue.extend({
	data() {
		return {
			title: "",
			content: ""
		};
	},
	methods: {
		postwrite() {
			this.$store
				.dispatch("WRITE_POST", {
					groupPk: this.getGroupPk,
					title: this.title,
					content: this.content
				})
				.then(data => {
					this.$router.push({
						name: "group/post",
						params: {
							postpk: data.pk
						}
					});
				})
				.catch(err => console.dir(err));
		}
	},
	computed: {
		getUser() {
			return this.$store.state.user;
		},
		getGroupPk() {
			return this.$route.params.pk;
		}
	}
});
